import React from 'react'
import style from './Card.module.css'

const Card = (props) => {
  return (
    <div className={style.mainContainer}>
      <p>name:{props.name}</p>
      <img src={props.img} className={style.cardImage} alt='foodImage'/>
      <ul>
        {props.genres.map((genre)=>(
          <li key={genre}>{genre}</li>
        ))}
      </ul>
    </div>
  )
}

export default Card

