import Card from "../Card/Card"
import style from './Cards.module.css'

const Cards = () => {

    const videoGames=[]
          
  return (
    <div className={style.mainContainer}>
        {   
            videoGames.map(element=>{
                return <Card
                    key={element.id}
                    id= {element.id}
                    name= {element.name}
                    img= {element.imagen}
                />
                
            })
        }
    </div>
  )
}

export default Cards
